<template>
    <div class="workAdd" >
        <navigation-bar
            pageName="创建工单"
            @onLeftClick="goBack"/>
        <div class="box">
            <div class="field-box">
                <van-field
                    readonly
                    clickable
                    label="小区"
                    :value="residential ? residential.AREA_NAME : ''"
                    input-align="right"
                    placeholder="请选择小区"
                    right-icon="arrow"
                    @click="selectResidential"
                    style="padding-left: 0.27rem!important;"
                    required/>
                <van-field
                    readonly
                    clickable
                    label="楼栋"
                    :value="building ? building.BUILD_NAME : ''"
                    input-align="right"
                    placeholder="请选择楼栋"
                    right-icon="arrow"
                    @click="selectBuilding"
                    style="padding-left: 0.27rem!important;"/>
                <van-field
                    readonly
                    clickable
                    label="房间号"
                    :value="room ? room.UNIN_ROOM_NAME : ''"
                    input-align="right"
                    placeholder="请选择房间号"
                    right-icon="arrow"
                    @click="selectRoom"
                    style="padding-left: 0.27rem!important;"/>
                <van-field
                    readonly
                    clickable
                    label="工单类型"
                    :value="workType ? workType.typeName : ''"
                    input-align="right"
                    placeholder="请选择工单类型"
                    right-icon="arrow"
                    @click="selectWorkType"
                    style="padding-left: 0.27rem!important;"
                    required/>
                <!--<van-field
                    readonly
                    clickable
                    label="报修方式"
                    :value="repairType ? repairType.wayName: ''"
                    input-align="right"
                    placeholder="请选择报修方式"
                    right-icon="arrow"
                    @click="selectRepairType"
                    style="padding-left: 0.27rem!important;"
                    required/>--><!-- 内部工单，默认为业主保修，没有保修类型选项， 隐藏此选项 -->
            </div>
            <div class="person-info" v-show="repairType">
                <van-field
                    clickable
                    label="报修人"
                    v-model="repairPersonInfo.repairName"
                    input-align="right"
                    placeholder="请选择报修人姓名"
                    :right-icon="repairType && repairType.wayCode == 1 ? '' : queryIcon"
                    @click-right-icon="showRepairPersonPopup"
                    :readonly="repairType && repairType.wayCode == 1"
                    style="padding-left: 0.27rem!important;"
                    required/>
                <van-field
                    clickable
                    label="报修人电话"
                    v-model="repairPersonInfo.repairTel"
                    input-align="right"
                    placeholder="请选择报修人电话"
                    :right-icon="repairType && repairType.wayCode == 1 ? '' : queryIcon"
                    @click-right-icon="showRepairPersonPopup"
                    :readonly="repairType && repairType.wayCode == 1"
                    style="padding-left: 0.27rem!important;border: 0;"
                    required/>
            </div>
            <div class="work-content" v-show="workType">
                <div class="title">选择工单标签</div>
                <div class="type">
                    <div @click="selectWorkPlanLabel(index)" v-for="(item,index) in labelList" :key="index" v-bind:class="{active: index == labelIndex}">
                      {{item.labelName}}
                    </div>
                </div>
                <div class="area-box">
                    <textarea name="" id="" placeholder="请输入工单内容..." v-model="workContent" maxlength="1000" @input="filterContent" class="content-textarea"></textarea>
                    <div class="voice-handle" v-if="voiceInputEnabled">
                        <van-row span="8" type="flex" justify="space-between">
                            <van-col span="8" class="desc" id="voice">
                              <span class="cls-voice">
                              <img class="cls-icon" src="../../assets/imgs/find/ic_yuyin.png" alt="">
                                语音录入
                              <img class="voice-input-hint" src="@/assets/imgs/find/search_voice.gif" alt="" v-show="showTips">
                              </span>
                            </van-col>
                            <van-col >
                              <p class="input-length">{{workContent.length}}/1000</p>
                            </van-col>
                        </van-row>
                    </div>
                    <div class="num" v-else>{{workContent.length}}/1000</div>
                </div>
            </div>
            <div class="photo-box">
                <div class="title">
                    <div>上传图片</div>
                    <div class="limit">(最多4张)</div>
                </div>
                <div class="photo">
                    <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" ref="addImageComponent"></add-image>
                </div>
            </div>
        </div>
        <div class="submit-box">
          <div class="submit" @click="saveWorkPlan">提交</div>
          <div class="submit" @click="saveWorkPlanNApply">提交并指派</div>
        </div>
        <van-popup v-model="showCondition" position="bottom" >
          <van-picker
            :columns="conditionList"
            show-toolbar
            :title="conditionTitle"
            :value-key="showKey"
            :default-index="defaultIndexList[selectedConditionType]"
            @cancel="showCondition = false"
            @confirm="seletedCondition"/>
        </van-popup>

        <van-popup v-model="showRepairPersonInfo" position="bottom" >
          <div class="repair-box">
            <div class="title">
              <div class="left">取消</div>
              <div class="center">选择业主</div>
              <div class="right">确定</div>
            </div>
            <div class="content">
              <div class="item" v-for="(item, index) in ownerList" :key="index" @click="selectOwner(item)">
                <div>{{item.ownerName}}</div>
                <div>{{item.phone}}</div>
              </div>
            </div>
          </div>
        </van-popup>

        <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          round
          close-on-click-action
          @select="addSelect"/>
    </div>
</template>

<script>
import {
  getVillageList,
  getBuildingList,
  getRoomList,
  getRoomOwnerList,
} from "../../services/homePageService";
import {
  getWorkPlanLabelList,
  saveWorkPlan,
  getWorkTypeList,
  getInterWorkPlanDetail,
} from "../../services/internalWorkPlanService";
import log from "../../utils/log"
import { store } from "../../utils/store";
import AddImageVue from "../addImage/AddImage.vue";
import queryIcon from '@/assets/imgs/workPlan/query.png';
import NavigationBar from "../title/NavigationBar";
import { Toast, Dialog } from "vant";
export default {
  name: "workAdd",
  data() {
    return {
      showCondition: false,
      showRepairPersonInfo: false,

      conditionList: [],
      showKey: "",
      defaultIndexList: [],
      selectedConditionType: 0,

      // 选择的条件
      residential: null,
      building: null,
      room: null,
      workType: null,
      repairType: {"wayName":"待业主保修","wayCode":1},// 内部工单，默认为代业主保修提报，没有业主保修选项， 设置默认值

      // 报修人信息
      repairPersonInfo: {
        repairCode: "",
        repairName: "",
        repairTel: ""
      },

      owner: null,

      labelIndex: -1,

      // 选择的条件列表
      residentialList: [],
      buildingList: [],
      roomList: [],
      // 工单类型
      workTypeList: [
        { typeName: "报修", typeCode: 0 },
        { typeName: "投诉", typeCode: 1 }
      ],
      // 报修方式
      repairTypeList: [
        { wayName: "业主电话报修", wayCode: 0 },
        { wayName: "自己发现", wayCode: 1 }
      ],
      // 业主列表
      ownerList: [],

      // picker框需要展示的列表中的哪个字段值
      showKey: "AREA_NAME",
      showKeyList: [
        "AREA_NAME",
        "BUILD_NAME",
        "showUnitRoomName",
        "typeName",
        "wayName"
      ],
      defaultIndexList: [0, 0, 0, 0, 0],

      conditionTypeList: [
        "选择小区",
        "选择楼栋",
        "选择房间号",
        "选择工单类型",
        "选择报修方式"
      ],
      defaultAreaCode: "",
      labelList: [],

      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      imgList: [],
      showAddSheet: false,
      // 工单内容
      workContent: "",

      userInfo: null,
      queryIcon: queryIcon,
      reloadList: false,
      workPlanSaving: false,
      workDetail: null,
      sendVal:false,
      speechStopped: true,
      posX: 0,
      posY: 0,
      showTips: false,
      waitStop: false,
      voiceInputEnabled: false,
    };
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  created: function() {
    // 获取用户信息
    this.userInfo = store.get("userInfo");
    this.defaultAreaCode = store.get("userInfo").AREA_CODE;
    this.getResidentialCondition();
    this.getWorkTypeList();
    this.setRepairType(this.repairType, 0);// 内部工单，默认为业主保修，没有保修类型选项， 设置默认值
    // 获取默认的小区的楼栋信息
    if(this.defaultAreaCode) {
      this.getBuildingCondition(this.defaultAreaCode);
    }
    window.onresize = () => {
        const { activeElement } = document;
        log.i('on resize event happened.' + activeElement.tagName);
        if (this.sendVal) {
            this.speechStopped = true;
            this.stopSpeech("界面变化");
            this.posX = 0;
            this.posY = 0;
        }
        if (activeElement.tagName === 'INPUT' || activeElement.tagName === 'TEXTAREA') {
            setTimeout(() => {
                activeElement.scrollIntoView();
            }, 100);
        }
    };
  },
  mounted() {
    if (this.voiceInputEnabled) {
      let oDiv = document.getElementById('voice');
      oDiv.addEventListener("touchstart", e => {
          this.speechStopped = false;
          this.goToSpeech(e);
      }, false);
      oDiv.addEventListener('touchmove', e => {
          if (!this.speechStopped) {
            if (Math.abs(this.posX - e.touches[0].clientX) > 30 || Math.abs(this.posY - e.touches[0].clientY) > 30) {
              this.speechStopped = true;
              this.stopSpeech("滑动操作");
            }
          }
      }, false);
      oDiv.addEventListener("touchend", e => {
          if (!this.speechStopped) {
            this.stopSpeech();
            this.posX = 0;
            this.posY = 0;
          }
      }, false);
      let audioPermitted = !store.get("audioPermitted") ? false : store.get("audioPermitted");
      if (!audioPermitted) {
          this.requestAudioPermission();
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    if(to.name == "workPlan"){
       if(this.reloadList) {
        to.meta.isUseCache = false;
      } else {
        to.meta.isUseCache = true;
      }
    }
    next();
  },
  methods: {
    goBack: function() {
      this.$router.back(-1);
    },

    /**
     * 列表选择事件
     */
    selectData: function(type) {
      this.showCondition = true;
      this.selectedConditionType = type;
      this.showKey = this.showKeyList[type];
    },

    /**
     * 选择小区的列表
     */
    selectResidential: function() {
      this.selectData(0);
      this.conditionList = this.residentialList;
    },

    /**
     * 选择楼座的列表
     */
    selectBuilding: function() {
      this.selectData(1);
      this.conditionList = this.buildingList;
    },

    /**
     * 选择房间的列表
     */
    selectRoom: function() {
      this.selectData(2);
      this.conditionList = this.roomList;
    },

    /**
     * 选择工单类型的列表
     */
    selectWorkType: function() {
      this.selectData(3);
      this.conditionList = this.workTypeList;
    },

    /**
     * 选择报修方式的列表
     */
    selectRepairType: function() {
      this.selectData(4);
      this.conditionList = this.repairTypeList;
    },

    /**
     * 选择标签事件
     */
    selectWorkPlanLabel: function(index) {
      this.labelIndex = index;
    },

    /**
     * 获取选择的小区列表
     */
    getResidentialCondition: function() {
      getVillageList({
        data: JSON.stringify({ USER_CODE: this.userInfo.USER_CODE })
      }).then(
        res => {
          this.residentialList = res;
          // 筛选出默认选择的小区
          this.setDefaultResidential();
        },
        error => {
          this.$toast(error);
        }
      );
    },

    getWorkTypeList: function() {
        getWorkTypeList({
                source: 0,
                page: 1, //分页页号
                rows: 9999 //每页条数
            }).then(
            res => {
                // update work type list
                this.workTypeList = res.data.list;
                //log.i(JSON.stringify(this.workTypeList));
            },
            error => {
                this.$toast(error);
            }
        );
    },

    /**
     * 设置默认的小区
     */
    setDefaultResidential: function() {
      this.residentialList.forEach((item, index) => {
        if (item.AREA_CODE == this.defaultAreaCode) {
          this.residential = item;
          this.defaultIndexList[0] = index;
        }
      });
    },

    /**
     * 获取选择的楼栋的列表
     */
    getBuildingCondition: function(areaCode) {
      getBuildingList({
        data: JSON.stringify({
          AREA_CODE: areaCode || this.residential.AREA_CODE
        })
      }).then(
        res => {
          this.buildingList = res;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取选择的房间号的列表
     */
    getRoomCondition: function() {
      getRoomList({
        data: JSON.stringify({ BUILD_CODE: this.building.BUILD_CODE })
      }).then(
        res => {
          this.roomList = res;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取房间管理者列表
     */
    getRoomOwnerList: function() {
      getRoomOwnerList({
          areaCode: this.residential.AREA_CODE
      }).then(
        res => {
          if(res.resultCode == 0) {
            this.ownerList = res.data.ownerList;
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取工单标签的列表
     */
    getWorkPlanLabelList: function(type) {
      getWorkPlanLabelList({
        type: type,
        deptCode: this.userInfo.deptCode,
      }).then(
        res => {
          if(res.resultCode == 0) {
            this.labelList = res.data;
          } else {
            this.$toast(res.msg)
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 选中条件(TODO 清空其他的条件)
     */
    seletedCondition: function(item, index) {
      this.showCondition = false;
      if (!item || item.length == 0) {
        return;
      }
      switch (this.selectedConditionType * 1) {
        case 0:
          this.setResidential(item, index);
          break;
        case 1:
          this.setBuilding(item, index);
          break;
        case 2:
          this.setRoom(item, index);
          break;
        case 3:
          this.setWorkType(item, index);
          break;
        case 4:
          this.setRepairType(item, index);
          break;
      }
    },
    /**
     * 设置小区信息
     */
    setResidential: function(item, index) {
      if (!this.residential || item.AREA_CODE != this.residential.AREA_CODE) {
        this.defaultIndexList[0] = index;
        this.residential = item;
        this.building = null;
        this.room = null;
        this.getBuildingCondition();
        this.resetRepairPersonInfo();
      }
    },

    /**
     * 设置楼栋信息
     */
    setBuilding: function(item, index) {
      if (!this.building || item.BUILD_CODE != this.building.BUILD_CODE) {
        this.defaultIndexList[1] = index;
        this.building = item;
        this.room = null;
        this.getRoomCondition();
        this.resetRepairPersonInfo();
      }
    },

    /**
     * 设置房间信息
     */
    setRoom: function(item, index) {
      if (!this.room || item.ROOM_CODE != this.room.ROOM_CODE) {
        this.defaultIndexList[2] = index;
        this.room = item;
        this.getRoomOwnerList();
        this.resetRepairPersonInfo();
      }
    },

    /**
      *  重置保修人姓名和报修人手机号
      **/
    resetRepairPersonInfo: function() {
        if (this.repairType != null && this.repairType.wayCode != 1
            && this.repairPersonInfo.repairName != "" && this.repairPersonInfo.repairTel != "") {
            this.repairPersonInfo.repairName = "";
            this.repairPersonInfo.repairTel = "";
        }
    },

    /**
     * 设置工单类型信息
     */
    setWorkType: function(item, index) {
      if (!this.workType || this.workType.typeCode != item.typeCode) {
        this.defaultIndexList[3] = index;
        this.workType = item;
        this.labelIndex = -1;
        this.getWorkPlanLabelList(item.typeCode);
      }
    },

    /**
     * 设置报修方式信息
     */
    setRepairType: function(item, index) {
      this.defaultIndexList[4] = index;
      this.repairType = item;
      this.selectOwner({
        ownerCode: item.wayCode == 1 ? this.userInfo.USER_CODE : "",
        ownerName: item.wayCode == 1 ? this.userInfo.USER_NAME : "",
        phone: item.wayCode == 1 ? this.userInfo.PHONE : ""
      });
    },

    /**
     * 展示报修人信息弹窗
     */
    showRepairPersonPopup: function() {
      if (!this.room) {
        this.$toast("请选择房间号");
        return;
      }
      this.showRepairPersonInfo = true;
    },

    /**
     * 选中报修人信息
     */
    selectOwner: function(item) {
      this.repairPersonInfo.repairCode = item.ownerCode;
      this.repairPersonInfo.repairName = item.ownerName;
      this.repairPersonInfo.repairTel = item.phone;
      this.showRepairPersonInfo = false;
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData){
      this.imgList = newImageData;
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex?this.$refs.addImageComponent.getPicture():this.$refs.addImageComponent.useCamera();
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.workContent = this.workContent.replace(/[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig, "");
    },

    /**
     * 保存工单
     */
    saveWorkPlan: function() {
      if(!this.hasCompleteParam()) {
        return;
      }
      if (this.workPlanSaving) {
          log.i("work plan saving now, ignore.");
          return;
      }
      this.workPlanSaving = true;
      var newWorkData = {
        terminalType: 1, //终端类型 1=物业端
        areaName: this.residential.AREA_NAME,
        areaCode: this.residential.AREA_CODE,
        ownerName: this.repairPersonInfo.repairName,
        ownerPhone: this.repairPersonInfo.repairTel,
        userCode: this.userInfo.USER_CODE,
        content: this.workContent,
        reportType: this.repairType.wayCode,
        typeFlag: 3, //0报修 1投诉 2验房 3内部
        picUrl: this.imgList,
        createdBy: this.userInfo.USER_CODE, // 用户信息
        postCode: this.userInfo.POST_CODE,
        innerTypeCode: this.workType.typeCode,//内部工单类型代码
        innerTypeName: this.workType.typeName,//内部工单类型名称
        labelCode:
          this.labelIndex > -1 ? this.labelList[this.labelIndex].labelCode : "",
        WJDM: "" //暂时不传
      };
      // set build info if building not null.
      if (this.building != null) {
          newWorkData.buildCode = this.building.BUILD_CODE;
          newWorkData.buildName = this.building.BUILD_NAME;
      }
      // set room info if room selected.
      if (this.room != null) {
          newWorkData.roomCode = this.room.ROOM_CODE;
          newWorkData.roomName = this.room.ROOM_NAME;
          newWorkData.unitNo = this.room.UNIT_NO;
      }
      saveWorkPlan(newWorkData).then(
        res => {
          if(res.resultCode=="0"){
            this.$toast("工单创建成功");
            this.$router.back(-1);
          } else {
            this.$toast(res.msg);
          }
          this.workPlanSaving = false;
        },
        error => {
          this.$toast(error);
          this.workPlanSaving = false;
        }
      ).catch(err => {
          this.workPlanSaving = false;
      });
    },

    /**
     * 保存并指派工单
     **/
    saveWorkPlanNApply: function() {
      if(!this.hasCompleteParam()) {
        return;
      }
      if (this.workPlanSaving) {
          log.i("work plan saving now, ignore.");
          return;
      }
      this.workPlanSaving = true;
      var newWorkData = {
        terminalType: 1, //终端类型 1=物业端
        areaName: this.residential.AREA_NAME,
        areaCode: this.residential.AREA_CODE,
        ownerName: this.repairPersonInfo.repairName,
        ownerPhone: this.repairPersonInfo.repairTel,
        userCode: this.userInfo.USER_CODE,
        content: this.workContent,
        reportType: this.repairType.wayCode,
        typeFlag: 3, //0报修 1投诉 2验房 3内部
        picUrl: this.imgList,
        createdBy: this.userInfo.USER_CODE, // 用户信息
        postCode: this.userInfo.POST_CODE,
        innerTypeCode: this.workType.typeCode,//内部工单类型代码
        innerTypeName: this.workType.typeName,//内部工单类型名称
        labelCode:
          this.labelIndex > -1 ? this.labelList[this.labelIndex].labelCode : "",
        WJDM: "" //暂时不传
      };
      // set build info if building not null.
      if (this.building != null) {
          newWorkData.buildCode = this.building.BUILD_CODE;
          newWorkData.buildName = this.building.BUILD_NAME;
      }
      // set room info if room selected.
      if (this.room != null) {
          newWorkData.roomCode = this.room.ROOM_CODE;
          newWorkData.roomName = this.room.ROOM_NAME;
          newWorkData.unitNo = this.room.UNIT_NO;
      }
      saveWorkPlan(newWorkData).then(
        res => {
          if(res.resultCode=="0"){
            this.$router.back(-1);
            console.log(res.data);
            this.getWorkPlanDetail(res.data);
          } else {
            this.$toast(res.msg);
          }
          this.workPlanSaving = false;
        },
        error => {
          this.$toast(error);
          this.workPlanSaving = false;
        }
      ).catch(err => {
          this.workPlanSaving = false;
      });
    },

    /**
    * 指派事件
    */
    assign: function() {
      this.$router.push({
       name: "workAssignInside",
       params: { workDetail: this.workDetail, isEvaluate: 1 }
      });
    },
    /**
    * 获取工单详情
    */
    getWorkPlanDetail: function(newComplCode) {
     getInterWorkPlanDetail({
       complCode: newComplCode,
       userCode: this.userInfo.USER_CODE
     }).then(
       res => {
         if(res.resultCode == 0) {
           this.workDetail = res.data;
           console.log("---" + JSON.stringify(this.workDetail));
           this.assign();
         } else {
           this.$toast(res.msg)
         }
       },
       error => {
           this.showErroInfo(error);
       }
     );
    },
    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      /*if(!this.building) {// 内部工单楼栋和房间号改为非必须，根据小区选择业主信息
        this.$toast("请选择楼栋！");
        return false;
      }
      if(!this.room) {
        this.$toast("请选择房间号！");
        return false;
      }*/
      if(!this.workType) {
        this.$toast("请选择工单类型！");
        return false;
      }
      if(!this.repairType) {
        this.$toast("请选择报修方式！");
        return false;
      }
      if(!this.repairPersonInfo.repairName) {
        this.$toast("请选择报修人！");
        return false;
      }
      if(!this.repairPersonInfo.repairTel) {
        this.$toast("请选择报修人电话！");
        return false;
      }
      if(!this.labelList || this.labelList.length <= 0) {
        this.$toast("工单标签未配置，请退出重试或联系管理员处理！");
        return false;
      }
      if(this.labelList && this.labelList.length > 0 && this.labelIndex < 0) {
        this.$toast("请选择工单标签！");
        return false;
      }
      if(!this.workContent) {
        this.$toast("请填写工单内容！");
        return false;
      }
      return true;
    },
    stopSpeech(msg){
        var _this = this;
        var ifly = cordova.plugins.IflyForXwjPlugin;
        if(_this.sendVal){
            _this.waitStop = true;
            if (msg) {
              _this.$toast(msg + ", 结束收音，正在识别。");
            } else {
              _this.$toast("长按停止，收音结束，正在识别。");
            }
            setTimeout (() => {
                if (_this.waitStop) {
                  _this.sendVal = false;
                  ifly.stopListen(null,null,null);
                  console.log("停止收音" + new Date().toString());
                }
            }, 3000);
        } else {
            Toast('说话时间太短，请长按说话');
            _this.sendVal = false;
        }
        //console.log("停止录音 "+ this.pressTimer);
        _this.showTips = false;
        clearTimeout(this.pressTimer);
        this.pressTimer = null;
    },
    //语音搜索
    goToSpeech(e) {
        var _this = this;
        this.pressTimer = null;
        var ifly = cordova.plugins.IflyForXwjPlugin;
        this.$toast("正在聆听");
        this.showTips = true;
        const platform = navigator.userAgent;
        const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        if(e.type === 'click' && e.button !==0){
            return;
        }
        if(this.pressTimer == null) {
          this.startSpeech(isiOS, ifly, false);
        }
    },
    startSpeech(isiOS, ifly, cycle) {
        var _this = this;
        this.pressTimer = setTimeout(()=>{
            if (!cycle) {
              console.log("start speech from touch action, set send val true.");
              _this.sendVal=true;
              if (_this.waitStop) {
                  // if wait stop now, should stop and reset wait stop state first.
                  _this.waitStop = false;
                  ifly.stopListen(null,null,null);
              }
            }
            if (isiOS) {
                ifly.startListen(function (result) {
                    if (result){
                        if (_this.sendVal){
                            var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                            let index = document.querySelector('.content-textarea').selectionStart;
                            let shouldScrollToEnd = index == _this.workContent.length;
                            let frontStr = _this.workContent.substring(0, index);
                            let backStr = _this.workContent.substring(index);
                            let newIndex = index + resultcatch.length;
                            let len = frontStr.length + resultcatch.length + backStr.length;
                            if (len <= 1000) {
                              _this.workContent = frontStr + resultcatch + backStr;
                            } else {
                              let res = frontStr + resultcatch + backStr;
                              _this.workContent = res.substring(0, 1000);
                            }
                            if (shouldScrollToEnd) {
                              document.querySelector('.content-textarea').scrollTop = 5000;
                            }
                            setTimeout(()=> {
                                document.querySelector('.content-textarea').selectionStart = newIndex;
                            }, 500);
                        }
                    }else {
                        console.log('没有值');
                    }
                    // restart voice catch before touch end
                    this.startSpeech(isiOS, ifly, true);
                },
                function (message) {
                    console.log("err" + message);
                    // restart voice catch before touch end
                    this.startSpeech(isiOS, ifly, true);
                }, false, true);
            }else {
                let audioPermitted = !store.get("audioPermitted") ? false : store.get("audioPermitted");
                if (!audioPermitted) {
                  this.$dialog.alert({
                      title: "授权语音权限",
                      message: "使用语音输入功能需要您先授权语音权限。",
                      showCancelButton: true
                  })
                  .then(() => {
                     // 请求语音权限
                     _this.requestAudioPermission();
                  })
                  .catch(() => { //点击取消按钮后的调用
                     console.log("点击了取消按钮");
                  })
                  // show dialog to request permission
                  return;
                }
                var list = [
                    'android.permission.RECORD_AUDIO'
                ];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(list,function (status) {
                    if(status.hasPermission){
                        ifly.startListen(function (result) {
                            if (result) {
                                if (_this.sendVal){
                                    console.log(result + new Date().toString());
                                    var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                                    let index = document.querySelector('.content-textarea').selectionStart;
                                    let shouldScrollToEnd = index == _this.workContent.length;
                                    let frontStr = _this.workContent.substring(0, index);
                                    let backStr = _this.workContent.substring(index);
                                    let newIndex = index + resultcatch.length;
                                    let len = frontStr.length + resultcatch.length + backStr.length;
                                    if (len <= 1000) {
                                      _this.workContent = frontStr + resultcatch + backStr;
                                    } else {
                                      let res = frontStr + resultcatch + backStr;
                                      _this.workContent = res.substring(0, 1000);
                                    }
                                    if (shouldScrollToEnd) {
                                      document.querySelector('.content-textarea').scrollTop = 5000;
                                    }
                                    setTimeout(()=> {
                                        document.querySelector('.content-textarea').selectionStart = newIndex;
                                    }, 500);
                                }
                            }else {
                                console.log('没有值');
                            }
                            // restart voice catch before touch end
                            _this.startSpeech(isiOS, ifly, true);
                        },
                        function (message) {
                            console.log("err" + message);
                            // restart voice catch before touch end
                            _this.startSpeech(isiOS, ifly, true);
                        }, false, true);
                    }
                },function (status) {
                    alert("请打开麦克风权限");
                });
            }
        },300);
    },
    requestAudioPermission() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      if (!isiOS) {
        var list = [
            'android.permission.RECORD_AUDIO'
        ];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(list,function (status) {
            if (status.hasPermission) {
                store.set('audioPermitted', true);
            } else {
                store.set('audioPermitted', false);
            }
        },function (status) {
            store.set('audioPermitted', false);
            this.$toast.alert("请打开麦克风权限");
        });
      }
    }
  },
  computed: {
    /**
     * 获取条件选择框的标题
     */
    conditionTitle: function() {
      return this.conditionTypeList[this.selectedConditionType];
    }
  }
};
</script>
